import classes from './About.module.css'

import about1 from '../../../images/about-1.jpeg'
import about2 from '../../../images/about-2.jpeg'

const About = () => {
  return (
    <div className={classes.about}>
      <div className={classes["about-1"]}>
        <p className={classes["about-text-1"]}>
          Zapoznaj si z opini innych na wybrany przez ciebie temat lub wyra swoj opini wypeniajc
          utworzone
          przez innych uytkownikw ankiety.
        </p>

        <div className={classes["about-image-1"]}>
          <img src={about1} alt="about1" />
        </div>

        <p className={classes["about-text-2"]}>
          Ankiety mog zawiera pytania jedno/wielo-krotnego wyboru, odpowiedzi w formie krtkiej wypowiedzi
          pisemnej,
          a take i obrazki.
        </p>

        <div className={classes["about-text"]}>
          <p className={classes["about-text-p1"]}>
            Zapoznaj si z opini innych na wybrany przez ciebie temat lub wyra swoj opini wypeniajc
            utworzone
            przez innych uytkownikw ankiety.
          </p>

          <p className={classes["about-text-p2"]}>
            Ankiety mog zawiera pytania jedno/wielo-krotnego wyboru, odpowiedzi w formie krtkiej
            wypowiedzi
            pisemnej,
            a take i obrazki.
          </p>
        </div>
      </div>

      <div className={classes["about-2"]}>
        <p className={classes["about-text-1"]}>
          Jako autor ankiety masz dostp do danych zebranych podczas udzielania odpowiedzi przez innych
          uytkownikw.
        </p>

        <div className={classes["about-image-2"]}>
          <img src={about2} alt="about2" />
        </div>

        <p className={classes["about-text-2"]}>
          Dane te s przedstawiane w postaci generowanych raportw, z ktrych wynika m.in jak odpowied na
          dane
          pytanie wybraa wikszo osb.
        </p>

        <div className={classes["about-text"]}>
          <p className={classes["about-text-p1"]}>
            Jako autor ankiety masz dostp do danych zebranych podczas udzielania odpowiedzi przez innych
            uytkownikw.
          </p>

          <p className={classes["about-text-p2"]}>
            Dane te s przedstawiane w postaci generowanych raportw, z ktrych wynika m.in jak odpowied
            na
            dane
            pytanie wybraa wikszo osb.
          </p>
        </div>
      </div>
    </div>
  )
}

export default About;