import { Link } from "react-router-dom";
import { useSelector } from 'react-redux'
import LoadingSpinner from "../../UI/LoadingSpinner";

const PollCreated = () => {

  const sentPoll = useSelector(state => state.sentPoll)
  const { loading } = sentPoll

  return (
    <>
      {
        !loading ?
          <div>
            <p>Ankieta zostaa utworzona! Ju teraz, uytkownicy w poszczeglnych grupach bd mogli wypeni dan ankiet. Maile zostay wysane z powiadomieniem o dostpnoci tej ankiety.</p>
            <Link to="/polls">Moje ankiety</Link>
          </div> : <LoadingSpinner />
      }
    </>
  )
}

export default PollCreated;