import classes from './Header.module.css'

const Header = () => {
  return (
    <header>
      <div className={classes["header-container"]}>
        <div className={classes["header-content"]}>
          <h1>Tworzenie wasnych ankiet z moliwoci generowania raportw</h1>
        </div>
      </div>
    </header>
  )
}

export default Header;