import { LinkContainer } from "react-router-bootstrap";
import Button from '../UI/Button'


const PollFinished = () => {
  return (
    <div>
      <p>Dzikujemy za wypenienie ankiety! Moesz teraz wrci do wyboru dostpnych ankiet.</p>
      <LinkContainer to="/polls">
        <Button>
          Moje ankiety
        </Button>
      </LinkContainer>
    </div>
  )
}

export default PollFinished;